import React, { memo } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import Link from '@material-ui/core/Link';
import Slide from '@material-ui/core/Slide';
import CloseIcon from '@material-ui/icons/Close';
import config from '../../config';
import styles from './styles';

const ShareModal = memo((props) => {
  const { share } = config;
  const { onClose, classes } = props;
  return (
    <Dialog
      open
      onClose={onClose}
      TransitionComponent={p => <Slide direction="up" {...p} />}
      keepMounted
      aria-labelledby="responsive-dialog-title"
    >
      <DialogTitle id="responsive-dialog-title">
        {share.dialogTitle}
        <IconButton aria-label="Close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogActions>
        <Button variant="raised" component={Link} href={`https://www.facebook.com/sharer.php?u=${encodeURIComponent(share.url)}&t=${encodeURIComponent(share.text)}`}>
          Facebook
        </Button>
        <Button variant="raised" component={Link} href={`https://twitter.com/intent/tweet?text=${encodeURIComponent(share.text)}%20${encodeURIComponent(share.url)}&via=${share.title.toLowerCase()}`}>
          Twitter
        </Button>
        <Button variant="raised" component={Link} href={`whatsapp://send?text=${encodeURIComponent(share.text)}%20${encodeURIComponent(share.url)}`}>
          Whatsapp
        </Button>
      </DialogActions>
    </Dialog>
  );
});

ShareModal.propTypes = {
  onClose: PropTypes.func,
  classes: PropTypes.object
};

export default withStyles(styles)(ShareModal);
