import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import Switch from '@material-ui/core/Switch';
import Link from '@material-ui/core/Link';
import { withStyles } from '@material-ui/core/styles';
import CodeIcon from '@material-ui/icons/Code';
import EmailIcon from '@material-ui/icons/Email';
import ShareIcon from '@material-ui/icons/Share';
import AddBoxIcon from '@material-ui/icons/AddBox';
import AssignmentIcon from '@material-ui/icons/Assignment';
import LiveTvTwoToneIcon from '@material-ui/icons/LiveTvTwoTone';
import { Plugins } from '@capacitor/core';
import Header from '../../components/Header';
import ShareModal from '../../components/ShareModal';
import Storage from '../../utils/storage';
import styles from './styles';
import config from '../../config';

const { Share } = Plugins;

class Settings extends PureComponent {
  static propTypes = {
    classes: PropTypes.object,
    theme: PropTypes.object,
    history: PropTypes.object,
    onChange: PropTypes.func
  };

  constructor(props) {
    super(props);
    this.state = {
      pushNotification: false,
      showShareModal: false
    };
  }

  onChangePushNotification = () => {
    const { pushNotification } = this.state;
    this.setState({ pushNotification: !pushNotification });
  }

  onChangeDarkMode = async () => {
    const { onChange, theme: { isDarkMode } } = this.props;
    await Storage.set('isDarkMode', !isDarkMode);
    onChange();
  }

  onShare = async () => {
    try {
      await Share.share(config.share);
    } catch (err) {
      this.setState({ showShareModal: true });
    }
  }

  onShareModalClose = () => this.setState({ showShareModal: false })

  onBackButtonClick = () => {
    const { history } = this.props;
    history.push('/');
  }

  render() {
    const { showShareModal } = this.state;
    const { classes, theme: { isDarkMode, mainColor } } = this.props;
    return (
      <React.Fragment>
        <Header title="Settings" backButton={this.onBackButtonClick} />
        <main className={classes.layout}>
          {showShareModal && <ShareModal onClose={this.onShareModalClose} />}
          <List component="nav">
            <ListItem button onClick={this.onChangeDarkMode}>
              <ListItemIcon>
                <LiveTvTwoToneIcon />
              </ListItemIcon>
              <ListItemText primary="Dark mode" />
              <ListItemSecondaryAction>
                <Switch
                  color={mainColor}
                  checked={isDarkMode}
                  onChange={this.onChangeDarkMode}
                />
              </ListItemSecondaryAction>
            </ListItem>
            <ListItem button onClick={this.onShare}>
              <ListItemIcon>
                <ShareIcon />
              </ListItemIcon>
              <ListItemText primary="Share app" />
            </ListItem>
            <ListItem
              button
              component={Link}
              className={classes.listLink}
              href={`${config.contactURL}?subject=Submit Newsletter`}
              target="_blank"
              rel="noopener noreferrer"
            >
              <ListItemIcon>
                <AddBoxIcon />
              </ListItemIcon>
              <ListItemText primary="Submit newsletter" />
            </ListItem>
            <ListItem
              button
              component={Link}
              className={classes.listLink}
              href={config.contactURL}
              target="_blank"
              rel="noopener noreferrer"
            >
              <ListItemIcon>
                <EmailIcon />
              </ListItemIcon>
              <ListItemText primary="Contact us" />
            </ListItem>
            <ListItem
              button
              component={Link}
              className={classes.listLink}
              href={config.termsURL}
              target="_blank"
              rel="noopener noreferrer"
            >
              <ListItemIcon>
                <AssignmentIcon />
              </ListItemIcon>
              <ListItemText primary="Terms of service" />
            </ListItem>
            <ListItem button>
              <ListItemIcon>
                <CodeIcon />
              </ListItemIcon>
              <ListItemText primary={`Version: ${config.appVersion}`} />
            </ListItem>
          </List>
        </main>
      </React.Fragment>
    );
  }
}

export default withStyles(styles, { withTheme: true })(Settings);
